export default {
		data(){
			return{
				subscribedCount: 0, //收藏数量
				SongList: [], //展示列表
				audio_id: null,
				audio_paused: null
			}
		},
		onLoad(params){
			this.getList(params)
		},
		methods:{
			go(){
				
			},
			getList(params){
				uni.showLoading({
				    title: "loading"
				});
				this.fly.get(`${this.baseApiUrl}/playlist/detail`,{id:params.id}).then(res => {
				    for (let item of res.playlist.tracks) {
				        this.SongList.push({
				            al: { picUrl: item.al.picUrl, name: item.al.name },
				            ar: item.ar,
				            name: item.name,
				            id: item.id
				        });
				    }
				    this.subscribedCount = res.playlist.subscribedCount;
				    uni.hideLoading();
				});
			
		}
	},
	}
